export class NewPostView {
  static render() {
    let link = document.querySelector("link");
    layout.innerHTML = template;
    document.title = "ÏÉà Ìè¨Ïä§Ìä∏ ÏûëÏÑ±";
    link.setAttribute("href", "src/css/newPost.scss?ver0.1");
    this.getRandomImg();
    this.post();
  }

  static getRandomImg() {
    const imgIcon = document.querySelector(".newImg");
    imgIcon.addEventListener("click", () => {
      imgIcon.innerHTML = "Ïù¥ÎØ∏ÏßÄ Ï∂îÍ∞ÄÏôÑÎ£å"
      imgIcon.disabled = true;
    })
  }

  static post() {
    const form = document.querySelector("form");
    form.onsubmit = e => {
      e.preventDefault();
      let formData = new FormData(form);
      let body = {
        image: "https://source.unsplash.com/random",
      }; 
      for (let [key, value] of [...formData]) {
        body[key] = value;
      }
      Api.post(POST_URL, body)
        .then(() => {
          location.hash = "";
          location.reload(true);
        })
        .catch((err) => console.log(err));
    };
  }
}
